import React, { useState } from 'react'
import { Button, Form, FormControl, InputGroup } from 'react-bootstrap'

export default function Register({ onRegister, language }) {

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [password2, setPassword2] = useState('')
  const [error, setError] = useState('')

  const texts = language.texts.loginRegisterScreen

  const [hidePassword, setHidePassword] = useState(true)
  const handlerSubmit = e => {
    e.persist();
    // eslint-disable-next-line
    const re = new RegExp("^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{7,32}$");
    const isOk = re.test(password);


    if (!isOk) {
      setError(texts.password.error)
      return
    }



  }

  const renderErrorMessage = () => {
    return error ? (
      <Form.Text className=" error">
        {error}
      </Form.Text>
    ) : null

  }

  return (
    <Form onSubmit={handlerSubmit}>
      <h1 className="L-R-Leabel">{texts.register.leabel}</h1>

      <Form.Group controlId="formBasicEmail">
        <Form.Label>{texts.email.leabel}</Form.Label>
        <Form.Control value={email} required type="email" onChange={e => setEmail(e.target.value)} placeholder={texts.email.placeholder} />

        <Form.Text className="text-muted">
          {texts.email.subText}
        </Form.Text>
      </Form.Group>

      <Form.Group controlId="formBasicPassword">
        <Form.Label>{texts.password.leabel}</Form.Label>
        <InputGroup className="mb-3">
          <FormControl
            value={password}
            onChange={e => setPassword(e.target.value)}
            required
            type={hidePassword ? 'password' : 'text'}
            placeholder={texts.password.placeholder}
          />
          <InputGroup.Append>
            <Button onClick={() => setHidePassword(!hidePassword)} variant={`${hidePassword ? "primary" : "danger"}`} >
              {hidePassword ? "Show" : "Hide"}
            </Button>
          </InputGroup.Append>
        </InputGroup>

      </Form.Group>
      <Form.Group controlId="formBasicPassword">
        <Form.Label>{texts.password.leabel2}</Form.Label>
        <InputGroup className="mb-3">
          <FormControl
            value={password2}
            onChange={e => setPassword2(e.target.value)}
            required
            type={hidePassword ? 'password' : 'text'}
            placeholder={texts.password.placeholder2}
          />

        </InputGroup>
        {renderErrorMessage()}
      </Form.Group>

      <Button variant="primary" type="submit">
        {texts.register.button}
      </Button>
    </Form>
  )
}
